<template>
  <div class="myPoints">
    <header>
      <div class="top">
        <div class="left">
          <p>零钱总额</p>
          <h1>{{list.money}}</h1>
        </div>
        <div class="right">
          <router-link :to="{name:'applyWithdraw',query:{type:1}}">
            <span class="sale">提现</span>
          </router-link>
        </div>
      </div>
      <div class="details">
        <div>
          <p>结算中</p>
          <p>{{list.withdraw}}</p>
        </div>
        <div class="weihu">
          <p>维护卡</p>
          <p>{{list.pay_currency}}</p>
        </div>
      </div>
    </header>
    <main>
      <h2>佣金明细</h2>
      <ul>
        <li v-for="item in list.list" :key="item.log_id">
          <div class="left">
            <h1>{{item.desc}}</h1>
            <p>{{item.change_time | formate}}</p>
          </div>
          <div class="right">{{item.pay_currency}}</div>
        </li>
      </ul>
    </main>

    <div
      id="0SuefKPV4p"
      v-show="removePopup"
      style="height: 100%; position: fixed; top: 0px; left: 0px; right: 0px; background-color: rgba(0, 0, 0, 0.7); z-index: 1000;  opacity: 1;"
    ></div>
    <div
      id="d2tfiX27YS"
      v-show="removePopup"
      class="popout-confirm popout-box"
      style="overflow: hidden; position: absolute; z-index: 1000; top: 50%; left: 50%; transform: translate3d(-50%, -50%, 0px); visibility: visible; border-radius: 4px; background: white; width: 312px; opacity: 1;"
    >
      <div class="confirm-content font-size-14">向卖家出示该二维码</div>
      <div class="content">
        <h1>我是{{buyList.nickname}}</h1>
        <h1>扫码快速跟我进行交易</h1>
        <img :src="buyList.qrcode" />
        <p>ID号：{{buyList.user_no}}</p>
      </div>

      <!--       <div class="btn-2-1" style="width: 47.5%;">
        <p
          class="js-cancel center font-size-16"
          style="padding-top: 5px;"
          @click="removePopup=false"
        >取消</p>
      </div>
      <div class="btn-2-1" style="width: 47.5%;">
        <p
          class="js-ok center c-green font-size-16"
          style="padding-top: 5px;"
          @click="removeConfirm"
        >确定</p>
      </div>-->
    </div>
    <div class="cancel" @click="cancel">
      <img src="../../assets/images/card_sd@2x.png" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import url from "@/axios/api.js";
export default {
  data() {
    return {
      list: [],
      removePopup: false,
      buyList: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("p", 1);
      axios.post(url.myCharge, params).then(res => {
        this.list = res.data.data;
      });
    },
    buy() {
      this.removePopup = true;
      let data = new URLSearchParams();
      data.append("openid", localStorage.getItem("openid"));
      data.append("type", "2");
      axios.post(url.saleCard, data).then(res => {
        this.buyList = res.data.data;
      });
    },
    cancel() {
      this.removePopup = false;
    }
  },
  filters: {
    formate: function(value) {
      let a = new Date(value);
      let b = a.toLocaleString();
      return b;
    }
  }
};
</script>


<style scoped lang="scss">
a {
  text-decoration: none;
}
.myPoints {
  padding: 10px;
  header {
    background: url(../../assets/images/change_bg@2x.png) no-repeat;
    background-size: 100% 100%;
    height: 175px;
    color: #fff;
    padding: 20px;
    .top {
      display: flex;
      .left {
        h1 {
          font-family: SourceHanSansCN-Regular;
          font-size: 45px;
          margin-bottom: 10px;
        }
        p {
          font-size: 13px;
        }
      }
      .right {
        margin-left: auto;
        display: flex;
        flex-direction: column;
        span {
          background: #fff;
          color: #2395ff;
          margin-top: 20px;
          padding: 5px 20px;
          border-radius: 20px;
        }
      }
    }
    .details {
      display: flex;
      width: 70%;
      justify-content: space-between;
      .weihu {
        border-left: 1px solid rgba(255, 255, 255, 0.4);
        padding-left: 20px;
      }
    }
  }
  main {
    padding: 10px;
    h2 {
      margin: 10px 0;
      font-size: 14px;
    }
    ul {
      li {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        .left {
          h1 {
            font-size: 15px;
            font-family: SourceHanSansCN-Regular;
            font-weight: 400;
          }
          p {
            font-size: 11px;
            font-family: SourceHanSansCN-Regular;
            font-weight: 400;
            color: rgba(164, 164, 164, 1);
            margin-top: 5px;
          }
        }
        .right {
          margin-left: auto;
          font-size: 17px;
          font-family: SourceHanSansCN-Medium;
          font-weight: 500;
          color: rgba(236, 174, 51, 1);
        }
      }
    }
  }
}

#d2tfiX27YS {
  .confirm-content {
    padding: 16px 0;
    background: #2395ff;
    font-size: 18px;
    font-family: SourceHanSansCN-Regular;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    text-align: center;
  }
  .content {
    padding: 20px 0;
    h1 {
      font-size: 18px;
      text-align: center;
    }
    img {
      height: 174px;
      width: 174px;
      display: block;
      margin: 0 auto;
    }
    p {
      text-align: center;
    }
  }
}
.cancel {
  z-index: 1000;
  top: 84%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0px);
  position: absolute;
  font-size: 5;
  height: 39px;
  width: 39px;
  img {
    width: 100%;
    height: 100%;
  }
}
</style>



